body {
  background: #fdfdfd;
  padding-top: 30px;
}

.form {
 
  min-width: 450px;
}
.input{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: 25px;
  margin-bottom: 25px;
  
}

.invalid-feedback {
  display: block;
}
.steps {
  display: flex;
  justify-content: space-between;
}
.step {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  flex-basis: 0;
  flex-grow: 1;
}
.step:not(:first-child)::before {
  position: absolute;
  content: "";
  height: 3px;
  width: 100%;
  background-color: #2196f3;
  left: -50%;
  top: 15.5px;
}
.step .step-number {
  z-index: 1;
  width: 35px;
  height: 35px;
  display: flex;
  border-radius: 50%;
  background: #2196f3;
  justify-content: center;
  align-items: center;
  color: white;
  font-weight: 800;
  border: 4px solid white;
}
.step.step-active .step-number {
  background: #00e5ff;
  border: 4px solid #616161;
}
.step.step-finish .step-number {
  background: #616161;
  border: 4px solid #616161;
}
.step.step-active:not(:first-child)::before {
  background: #616161;
}
.step.step-finish:not(:first-child)::before {
  background: #616161;
}
